import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { Button, Header , Main , Container,Carousel,CarouselItem,image} from 'element-ui';
Vue.config.productionTip = false
Vue.use(Button)
Vue.use(Header)
Vue.use(Main)
Vue.use(Container)
Vue.use(Carousel)
Vue.use(CarouselItem)
Vue.use(image)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
